@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
* {
  font-family: 'Open Sans', sans-serif; }
  *::before, *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0; }

body {
  font-family: 'Open Sans', sans-serif; }

.app {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem; }
  .app .search {
    padding: 2rem 0;
    display: flex;
    justify-content: center;
    align-items: center; }
    .app .search input {
      width: 100%;
      max-width: 500px;
      padding: 1rem;
      border-radius: 2px;
      border: 1px solid #ddd;
      -webkit-transition: all ease-in-out 250ms;
      transition: all ease-in-out 250ms; }
      .app .search input:hover, .app .search input:focus {
        border-color: #0084ff;
        outline: none; }
  .app .robots {
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 1fr 1fr 1fr; }
    .app .robots .robot {
      padding: 2rem;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      border-radius: 5px;
      margin-bottom: 1rem;
      -webkit-transition: all ease-in-out 250ms;
      transition: all ease-in-out 250ms;
      cursor: pointer; }
      .app .robots .robot:hover {
        background: #f0fdff; }
      .app .robots .robot img {
        width: 200px;
        height: 200px;
        padding-bottom: 1rem; }
      .app .robots .robot h2 {
        padding: 0;
        margin: 0; }
      .app .robots .robot p {
        padding: 0;
        margin: 0; }

